[data-page="Home"] {
  background: white;
  position: relative;
  z-index: 1;
}
@keyframes bannerAni {
  0% {
    transform: scale(1.15);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
[data-page="Home"] .icon {
  margin-right: 5px;
}
[data-page="Home"] .banner {
  position: relative;
  width: 100%;
  height: 52vh;
  overflow: hidden;
  letter-spacing: 1px;
  text-shadow: 0px 0px 5px #222;
}
[data-page="Home"] .banner img {
  width: 100%;
  height: 111%;
  object-fit: cover;
  animation: bannerAni 30s infinite;
  animation: bannerAni linear 30s infinite;
}
[data-page="Home"] .banner .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 5% 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  font-size: 1.05vw;
  text-align: center;
  background: rgba(0, 0, 0, 0.5);
}
[data-page="Home"] .banner .overlay .content {
  display: flex;
  flex-direction: column;
  align-items: space-evenly;
  max-width: 45vw;
}
[data-page="Home"] .banner .overlay > :nth-child(2) {
  /*margin-top: 80px;*/
}
[data-page="Home"] .banner .overlay > :nth-child(2) #ourSpecialties,
[data-page="Home"] .banner .overlay > :nth-child(2) > :nth-child(2) {
  text-transform: uppercase;
}
[data-page="Home"] .banner .overlay > :nth-child(2) #ourSpecialties {
  font-weight: 900;
  font-size: 180%;
}
[data-page="Home"] .banner .overlay > :nth-child(2) #ourSpecialties .item {
  display: none;
  opacity: 0;
}
[data-page="Home"] .banner .overlay > :nth-child(2) #ourSpecialties .item.active {
  display: block;
  opacity: 1;
}
[data-page="Home"] .banner .icon {
  position: absolute;
  z-index: 2;
  left: 0px;
  top: auto;
  bottom: 0px;
  font-size: 8vw;
  text-align: center;
  width: 100%;
}
[data-page="Home"] .banner .icon:before,
[data-page="Home"] .banner .icon:after {
  height: auto;
  cursor: pointer;
  position: relative;
  animation-name: bounce;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}
[data-page="Home"] #bridge {
  margin: 3em 0 0;
  padding-bottom: 8em;
}
[data-page="Home"] #bridge #whatWeDo {
  padding: 0 22%;
}
[data-page="Home"] #bridge #whatWeDo .head {
  font-weight: 900;
}
[data-page="Home"] #bridge #whatWeDo .content {
  font-size: 1.2em;
}
[data-page="Home"] #bridge #categories {
  width: 100%;
  margin: 8em 0 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 4em;
  padding: 0 8vw;
}
[data-page="Home"] #bridge #categories .category {
  width: 100%;
}
[data-page="Home"] #bridge #categories .category .preview {
  position: relative;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 0px 2em #b9b9b9;
  transition: all 0.3s linear;
}
[data-page="Home"] #bridge #categories .category .preview img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}
[data-page="Home"] #bridge #categories .category .preview:before {
  content: "";
  display: block;
  padding-top: 100%;
  padding-top: 80%;
}
[data-page="Home"] #bridge #categories .category .preview:hover {
  transform: scale(1.08, 1.08);
}
[data-page="Home"] #bridge #categories .category .label {
  font-weight: 700;
  color: #464646;
  margin: 1.3em 0 0;
}
[data-page="Home"] #workedWith {
  background: #f7f7fe;
  padding: 2vw 8vw;
}
[data-page="Home"] #workedWith .head {
  margin: 0 0 2em;
}
[data-page="Home"] #workedWith #clients {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 6vw;
  justify-content: center;
  width: 100%;
}
[data-page="Home"] #workedWith #clients .client {
  font-size: 3vw;
  color: #747474;
}
